import { Form, Input, Button, Select, Row, Col, Card, InputNumber } from 'antd';
import { InfoCircleOutlined } from '@ant-design/icons';
import { useState } from 'react';

import UploadImgs from '../../../commons/UploadImgs';

const AddFoodForm = () => {
    const { TextArea } = Input;
    const { Option } = Select;

    const [form] = Form.useForm();
    const [requiredMark, setRequiredMarkType] = useState('optional');

    const onRequiredTypeChange = ({ requiredMarkValue }) => {
        setRequiredMarkType(requiredMarkValue);
    };

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
    };

    return (
        <Form
            form={form}
            layout='vertical'
            initialValues={{
                requiredMarkValue: requiredMark,
            }}
            onValuesChange={onRequiredTypeChange}
            requiredMark={requiredMark}
            onFinish={onFinish}
            text>
            <Form.Item
                label={<label style={{ color: 'black' }}>Nombre</label>}
                required>
                <Input placeholder='Ingrese el nombre del alimento' />
            </Form.Item>
            <Form.Item
                name='image'
                wrapperCol={{ sm: 24 }}
                label={<label style={{ color: 'black' }}>Imagen</label>}
                tooltip={{
                    title: 'Seleccione una imagen del alimento',
                    icon: <InfoCircleOutlined />,
                }}>
                <UploadImgs />
            </Form.Item>

            {/*CONOS*/}
            <Card title='conos'>
                <Row>
                    <Col span={12}>
                        <Form.Item
                            wrapperCol={{ sm: 24 }}
                            label={
                                <label style={{ color: 'black' }}>
                                    cono nutricional
                                </label>
                            }
                            tooltip={{
                                title: 'Seleccione el cono nutricional',
                                icon: <InfoCircleOutlined />,
                            }}>
                            <Input placeholder='Cargue el cono nutricional del alimento' />
                            <Button type='primary'>Cargar cono</Button>
                        </Form.Item>
                    </Col>

                    <Col span={12}>
                        <Form.Item
                            wrapperCol={{ sm: 24 }}
                            label={
                                <label style={{ color: 'black' }}>
                                    cono ambiental
                                </label>
                            }
                            tooltip={{
                                title: 'Seleccione el cono ambiental',
                                icon: <InfoCircleOutlined />,
                            }}>
                            <Input placeholder='Cargue el cono ambiental del alimento' />
                            <Button type='primary'>Cargar cono</Button>
                        </Form.Item>
                    </Col>
                </Row>

                <Row>
                    <Col span={12}>
                        <Form.Item
                            wrapperCol={{ sm: 24 }}
                            label={
                                <label style={{ color: 'black' }}>
                                    cono economa
                                </label>
                            }
                            tooltip={{
                                title: 'Seleccione el cono de economa',
                                icon: <InfoCircleOutlined />,
                            }}>
                            <Input placeholder='Cargue el cono de economa' />
                            <Button type='primary'>Cargar cono</Button>
                        </Form.Item>
                    </Col>

                    <Col span={12}>
                        <Form.Item
                            wrapperCol={{ sm: 24 }}
                            label={
                                <label style={{ color: 'black' }}>
                                    cono de cultura sociedad
                                </label>
                            }
                            tooltip={{
                                title: 'Seleccione el cono de cultura sociedad',
                                icon: <InfoCircleOutlined />,
                            }}>
                            <Input placeholder='Cargue el cono de cultura sociedad' />
                            <Button type='primary'>Cargar cono</Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Card>
            {/*OPCIONES DE PREPARACIN AQU*/}
            <br />
            {/*CANTIDAD DE ALIMENTOS*/}
            <Card title='Cantidad de alimento'>
                <Form.Item
                    label={
                        <label style={{ color: 'black' }}>
                            Cantidad sugerida
                        </label>
                    }
                    required>
                    <Select defaultValue='0' style={{ width: 120 }}>
                        {Array.from({ length: 15 }).map((_, index) => (
                            <option value={index} key={index}>
                                {index}
                            </option>
                        ))}
                    </Select>
                </Form.Item>
                <Row gutter={[8, 8]}>
                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>Unidad</label>
                            }
                            required>
                            <Input placeholder='Ingrese la unidad' />
                        </Form.Item>
                    </Col>

                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>
                                    Peso neto
                                </label>
                            }
                            required>
                            <Input placeholder='Ingrese el peso neto' />
                        </Form.Item>
                    </Col>
                </Row>
            </Card>
            <br />

            {/*CALORAS MACRONUTRIENTES*/}
            <Card title='Cantidad de alimento'>
                <Row gutter={[8, 8]}>
                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>
                                    Energa
                                </label>
                            }
                            required>
                            <Input placeholder='Ingrese la cantidad de energa' />
                        </Form.Item>
                    </Col>

                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>
                                    Protenan
                                </label>
                            }
                            required>
                            <Input placeholder='Ingrese la cantidad de protena' />
                        </Form.Item>
                    </Col>
                </Row>

                <Row gutter={[8, 8]}>
                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>
                                    Grasas saturadas
                                </label>
                            }
                            required>
                            <Input placeholder='Ingrese la cantidad de grasas saturadas' />
                        </Form.Item>
                    </Col>

                    <Col span={12}>
                        <Form.Item
                            label={
                                <label style={{ color: 'black' }}>
                                    Grasas monoinsaturados
                                </label>
                            }
                            required>
                            <Input placeholder='Ingrese la cantidad de grasas monoinsaturados' />
                        </Form.Item>
                    </Col>
                </Row>
            </Card>

            <Form.Item
                label={
                    <label style={{ color: 'black' }}>Grupo exportable</label>
                }
                required>
                <Input placeholder='Ingrese el grupo exportable' />
            </Form.Item>
            <Form.Item
                label={
                    <label style={{ color: 'black' }}>
                        Sub grupo exportable
                    </label>
                }
                required>
                <Input placeholder='Ingrese el grupo sub exportable' />
            </Form.Item>
            <Form.Item
                label={
                    <label style={{ color: 'black' }}>
                        Clasificacin exportable
                    </label>
                }
                required>
                <Input placeholder='Ingrese la clasificacin exportable' />
            </Form.Item>
            <Form.Item
                label={
                    <label style={{ color: 'black' }}>Grupo de alimento</label>
                }
                required>
                <Input placeholder='Ingrese el grupo de alimento al que pertenece' />
            </Form.Item>
            <Form.Item
                label={<label style={{ color: 'black' }}>Mensaje</label>}
                required>
                <TextArea rows={2} placeholder='Ingrese el mensaje' />
            </Form.Item>
            <Form.Item
                label={
                    <label style={{ color: 'black' }}>
                        Opciones de preparacin
                    </label>
                }
                required>
                <Input placeholder='Ingrese las opciones de preparacin' />
            </Form.Item>

            <Form.Item
                label={
                    <label style={{ color: 'black' }}>
                        Caloras de alimentos
                    </label>
                }
                required>
                <Select defaultValue='0' style={{ width: 120 }}>
                    {Array.from({ length: 15 }).map((_, index) => (
                        <option value={index} key={index}>
                            {index}
                        </option>
                    ))}
                </Select>
            </Form.Item>
            <Form.Item>
                <Button type='primary' htmlType='submit'>
                    Submit
                </Button>
            </Form.Item>
        </Form>
    );
};

export default AddFoodForm;
